import React from "react";
import PropTypes from "prop-types";

export const Button = ({ type, label, style, onClick, ...props }) => {
  return (
    <button
      type="button"
      className={`[button--${type}]`}
      style={style}
      {...props}
    >
      <div>{label}</div>
    </button>
  );
};

Button.propTypes = {
  label: PropTypes.string,
  style: PropTypes.object,
  onClick: PropTypes.func,
};

Button.defaultProps = {
  label: "Press Me",
  style: { margin: "0px" },
  onClick: undefined,
};

export default Button;
